import route from "@/common/public.js"
import uniPopup from '@/components/uni-popup/uni-popup.vue'
import {
	mapGetters,
	mapActions
} from 'vuex';


export default {
	components: {uniPopup},
	computed: {
		...mapGetters(['statusBarHeight'])
	},
	data() {
		return {
			payMoney: '',  //支付总金额
			amount: '',  //可用信用额度
			usable: '',  //剩余可用信用额度
			orderId: '',  //订单id
			Signboard: '', //令牌
			Signguid: '',  //id
			pytPwd:false,
			trade_pwd: '',
			focus: true,
			show: false,
			numarr: [],
			d_time: 10,
			nextsteep: false,
			tel: 0,
			numlength: "",
			gopen: false,
			isactive: true,
			resgetcode: false,
			boardlists: [{
					id: 1,
					con: 1,
					checked: false
				}, {
					id: 2,
					con: 2,
					checked: false
				}, {
					id: 3,
					con: 3,
					checked: false
				}, {
					id: 4,
					con: 4,
					checked: false
				}, {
					id: 5,
					con: 5,
					checked: false
				}, {
					id: 6,
					con: 6,
					checked: false
				}, {
					id: 7,
					con: 7,
					checked: false
				}, {
					id: 8,
					con: 8,
					checked: false
				}, {
					id: 9,
					con: 9,
					checked: false
				}, {
					id: 10,
					con: "",
					checked: false
				}, {
					id: 11,
					con: 0,
					checked: false
				},
				{
					id: 12,
					con: "",
					checked: false
				},

			]
		};
	},
	computed: {
		len: function() {
			if (this.trade_pwd.length == 6) {
				// 第一次进来可以点击的
			}
			return this.trade_pwd.length
		}
	},
	onLoad(options) {
		let jsonList = uni.getStorageSync("jsonList");  //获取存储在Storage里的值
		let jsondata = JSON.parse(jsonList); //JSON字符串转对象
		this.Signboard = jsondata.Ident_Signboard; //令牌
		this.Signguid = jsondata.Ident_Signguid; //用户id
		this.payMoney = options.money
		this.amount = options.amount
		this.usable = options.usable
		this.orderId = options.orderNum
	},
	methods: {
		
		pytClick(evt){
			this.pytPwd=true;
		},
		writepwd(num) {
			if (num == 12) {
				this.close();
				return;
			}
			if (num == 10) {
				return;
			}
			if (num == 11) {
				num = 0;
				this.boardlists[10].checked = true;
			} else {
				this.boardlists[num - 1].checked = true;
			}
			this.numarr.push(num);
			if (this.numarr.length > 6) {
				return
			}
			var that = this;
			setTimeout(function() {
				for (var i = 0; i < that.boardlists.length; i++) {
					that.boardlists[i].checked = false;
				}
			}, 200)
			this.trade_pwd = this.numarr.join("");


		},
		//密码框
		back() {
			uni.navigateBack();
		},
		//回退方法
		close() {
			this.numarr.pop();
			if (this.trade_pwd.length > 0) {
				this.trade_pwd = this.trade_pwd.substring(0, this.trade_pwd.length - 1);
			}
		},
		blur1() {
			this.show = false
		},
		focus1() {
			this.show = true
		},

		// 确认执行的方法
		setpwd() {
			if (this.trade_pwd.length < 6) {
				uni.showToast({
					title: '密码长度不能少于6位',
					icon: 'none'
				})
				return;
			}
			// 密码长度为6位以后执行方法
			uni.request({
				url: route.variable+'/mobile/Balance/yuepay',
				method: 'POST',
				data: {
					Ident_Signboard: this.Signboard,
					Ident_Signguid: this.Signguid,
					order_num: this.orderId,  //订单号
					paypwd: this.trade_pwd, //支付密码
					money: this.payMoney,  //支付金额
				},
				success: (res) => {
					if(res.data.status == 0){
						uni.showToast({
							title: '支付成功',
							icon: 'none',
							duration:3000,
						})
						setTimeout(function(){
							uni.redirectTo({
								url: 'orderpageinfo?orderNum=balance'
							})
						},3000);
					}else{
						uni.showToast({
							title: '支付失败，'+res.data.message,
							icon: 'none'
						})
					}
				},
				fail:(res)=>{
					uni.hideLoading()
					uni.showToast({title: '支付失败'+'错误码201',icon:"none"});
				}
			})
		},
	},
	// 返回：如果不支付直接点击返回的话 就直接跳转到我的订单页面
	// onBackPress(options) {
	//    if (options.from == "navigateBack" || options.from == "backButton") {
	//    		uni.redirectTo({
	//    			url: 'orderpageinfo?orderNum=balance'
	//    		})
	// 		return true;
	//    }
	// }
}
